
import "../assets/scss/_navigation.scss";
import logo from "../assets/imgs/Skyz-logo.png";
import React, { useState, useEffect, useRef } from "react";
import { Link, useLocation } from "react-router-dom";
import i18next from "i18next";
import { useTranslation } from "react-i18next";

const lngs = {
  en: { nativeName: "En" },
  he: { nativeName: "He" },
};

const AboutFlyout = ({ onClose }) => {
  const { t } = useTranslation();

  const handleLinkClick = () => {
    onClose(); // Close the flyout when a link is clicked
  };

  return (
    <div className="about-flyout">
      <Link
        to="/skyzcrm---/-skyzcrm/"
        className="link"
        onClick={handleLinkClick}
      >
        {t("nav-about")}
      </Link>
      <Link
        to="/skyzcrm---/-skyzcrm/cookie-policy/"
        className="link"
        onClick={handleLinkClick}
      >
        Cookie Policy
      </Link>
      <Link
        to="/skyzcrm---/-skyzcrm/terms-of-service/"
        className="link"
        onClick={handleLinkClick}
      >
        Terms of Service
      </Link>
      <Link
        to="/skyzcrm---/-skyzcrm/privacy-policy/"
        className="link"
        onClick={handleLinkClick}
      >
        Privacy Policy
      </Link>
    </div>
  );
};

const Navbar = ({ onToggleDarkMode, darkMode }) => {
  const [menuActive, setMenuActive] = useState(false);
  const [aboutFlyoutActive, setAboutFlyoutActive] = useState(false);
  const navbarRef = useRef(null);

  useEffect(() => {
    const handleOutsideClick = (event) => {
      if (navbarRef.current && !navbarRef.current.contains(event.target)) {
        setMenuActive(false);
        setAboutFlyoutActive(false);
      }
    };

    document.addEventListener("click", handleOutsideClick);

    return () => {
      document.removeEventListener("click", handleOutsideClick);
    };
  }, []);

  const handleMenuItemClick = () => {
    setMenuActive(false);
    setAboutFlyoutActive(false);
  };

  const handleAboutClick = () => {
    setAboutFlyoutActive(!aboutFlyoutActive);
  };

  const { t, i18n } = useTranslation();
  const location = useLocation();
  const currentPath = location.pathname;

  return (
    <header className="navbar" ref={navbarRef}>
      <Link to="/">
        <img src={logo} alt="skyz-logo" className="logo" />
      </Link>
      <div className={`links ${menuActive && "active"}`}>
        <Link to="/" className="link" onClick={handleMenuItemClick}>
          {t("nav-home")}
        </Link>
        {/* Add more links as needed */}

        {/* About section with flyout */}
        <span className="link" onClick={handleAboutClick} >
          {t("nav-about")}
        </span>
        {aboutFlyoutActive && (
          <AboutFlyout onClose={() => setAboutFlyoutActive(false)} />
        )}

        <Link
          to="https://skyzcrm.co.il/"
          className="link"
          onClick={handleMenuItemClick}
        >
          {t("nav-login")}
        </Link>
        <Link
          to="/skyzcrm---/-/"
          className="link"
          onClick={handleMenuItemClick}
        >
          {t("nav-contact")}
        </Link>
        <span
          className="material-symbols-outlined darkmode-toggle"
          onClick={onToggleDarkMode}
        >
          {darkMode ? "light_mode" : "dark_mode"}
        </span>
        <div className="lang">
          {currentPath !== "/%D7%91%D7%9C%D7%95%D7%92/" &&
            Object.keys(lngs).map((lng) => (
              <button
                className={`lang-btn ${
                  i18n.resolvedLanguage === lng ? "disabled" : ""
                }`}
                type="submit"
                key={lng}
                onClick={() => i18next.changeLanguage(lng)}
                disabled={i18n.resolvedLanguage === lng}
              >
                {lngs[lng].nativeName}
              </button>
            ))}
        </div>
      </div>
      <span
        className="material-symbols-outlined menu-icon"
        onClick={() => setMenuActive(!menuActive)}
      >
        menu
      </span>
    </header>
  );
};

export default Navbar;
